import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import { Select } from 'element-react';

const Container = styled.footer`
  font-size: 12px;
  height: 20px;
  line-height: 20px;
  margin: 20px 0;
  text-align: center;
  color: #bbb;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 485px) {
    margin: 10px 0;
  }
  @media screen and (max-width: 1024px) and (orientation: landscape) {
    margin: 5px 0;
  }
`;

const StyledSelect = styled(Select)`
  margin-right: 50px;

  @media (max-width: 485px) {
    margin-right: 10px;
    width: 120px;
  }
`;

const Footer = ({ onLanguageChange }) => {
  const { t, i18n } = useTranslation();

  return (
    <Container>
      <StyledSelect
        value={i18n.language}
        onChange={lang => onLanguageChange(lang)}
      >
        <StyledSelect.Option
          label="English"
          value="en"
          key="en"
        ></StyledSelect.Option>
        <StyledSelect.Option
          label=""
          value="ja"
          key="ja"
        ></StyledSelect.Option>
        <StyledSelect.Option
          label=""
          value="zh-TW"
          key="zh-TW"
        ></StyledSelect.Option>
      </StyledSelect>
      {t('copyright')}
    </Container>
  );
};

Footer.propTypes = {
  onLanguageChange: PropTypes.func.isRequired,
};

export default Footer;
